  background: url("../img/wave-center-down.png"), url("../img/wave-center-down.png"), url("../img/pattern-crop.jpg"), linear-gradient(to top, #f6f8e2 96%, transparent);
  background-repeat: repeat-x, repeat-x, repeat-x;
  background-position: 0 0, 0 1150px, 0 1200px, 0 0;
  min-height: 1465px;
  overflow-x: hidden;
}

