.progress__wrapper {
  width: 1140px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (max-width: 1196px) and (min-width: 768px) {
  .progress__wrapper {
    width: 720px;
  }
}

@media (max-width: @tablet-width) and (min-width: 480px) {
  .progress__wrapper {
    width: 440px;
    padding-top: 0;
    padding-bottom: 20px;
  }
}

@media (max-width: @mobile-width) {
  .progress__wrapper {
    width: 300px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.progress__wrapp-blocks {
  position: relative;
  width: 1140px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 65px;
  margin: 0 auto;
}

@media (max-width: 1196px) and (min-width: 768px) {
  .progress__wrapp-blocks {
    flex-wrap: wrap;
    width: 620px;
    padding-top: 20px;
  }
}

@media (max-width: @tablet-width) and (min-width: 480px) {
  .progress__wrapp-blocks {
    width: 440px;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 15px;
  }
}

@media (max-width: @mobile-width) {
  .progress__wrapp-blocks {
    width: 260px;
    flex-direction: column;
    padding-top: 15px;
  }
}

@media (max-width: @tablet-width) and (min-width: 480px) {
  .progress__wrapp-blocks--page-history,
  .progress__wrapp-blocks--page-baza {
    padding-top: 45px;
  }
}

.progress__wrapp-blocks--diplom::after {
  position: absolute;
  top: 120px;
  left: 800px;
  content: url("../img/diploma.png");
}

@media (max-width: 1196px) and (min-width: 768px) {
  .progress__wrapp-blocks--diplom::after {
    top: 1020px;
    left: 215px;
  }
}

@media (max-width: @tablet-width) and (min-width: 480px) {
  .progress__wrapp-blocks--diplom::after {
    top: 1085px;
    left: 180px;
    content: url("../img/diploma-planshet.png");
  }
}

@media (max-width: @mobile-width) {
  .progress__wrapp-blocks--diplom::after {
    top: 2770px;
    left: -30px;
    content: url("../img/diploma-mobile.png");
  }
}

.progress__list {
  width: 260px;
}

@media (max-width: 1196px) and (min-width: 768px) {
  .progress__list {
    margin-top: 55px;
  }
}

@media (max-width: @tablet-width) and (min-width: 480px) {
  .progress__list {
    width: 200px;
    margin-top: 55px;
  }
}

@media (max-width: @mobile-width) {
  .progress__list {
    width: 260px;
    margin-top: 75px;
  }
}

@media (max-width: @mobile-width) {
  .progress__list:first-of-type {
    margin-top: 55px;
  }
}

@media (max-width: 1196px) and (min-width: 768px) {
  .progress__list--page-history,
  .progress__list--person {
    margin-top: 35px;
  }
}

@media (max-width: @tablet-width) and (min-width: 480px) {
  .progress__list--page-history {
    margin-top: 10px;
  }
}

@media (max-width: @mobile-width) {
  .progress__list--page-history,
  .progress__list--person {
    width: 260px;
    margin-top: 35px;
  }
}

.progress__lead {
  font-family: AntiQuasi;
  font-size: 20px;
  line-height: 25px;
  letter-spacing: -0.3px;
  text-align: left;
  color: #d71c6d;
  margin: 0;
  margin-bottom: 38px;
}

@media (max-width: @tablet-width) {
  .progress__lead {
    min-height: 130px;
  }
}

@media (max-width: @mobile-width) {
  .progress__lead {
    min-height: 92px;
  }
}

.progress__lead span {
  display: block;
  font-size: 75px;
  margin-bottom: 30px;
}

.progress__icon {
  margin-top: 38px;
  margin-bottom: 20px;
}


